export const renderContentNoti = (status, desc) => {
    switch (status) {
      case "LOGIN_SUCCESS":
        return {
          message: "ng nhp thnh cng",
          description: "Bn  ng nhp thnh cng!"
        }
  
      case "LOGIN_FAIL":
        return {
          message: "ng nhp tht bi",
          description: "Vui lng kim tra li tn ng nhp hoc mt khu!"
        }
  
      case "REGISTER_SUCCESS":
        return {
          message: "ng k ti khon thnh cng",
          description: "Bn  ng k ti khon thnh cng!"
        }
  
      case "REGISTER_FAIL":
        return {
          message: "ng k ti khon tht bi",
          description: desc ? desc : "Vui lng kim tra li thng tin!"
        }
  
      default:
        return {
          message: "H thng ang chay",
          description: "H thng ang chay"
        }
    }
  }
  